@font-face {
    font-family: 'Optima';
    src: url('fonts/Optima-Regular.eot');
    font-weight: normal;
    font-style: normal;
}
@font-face {
    font-family: 'Optima';
    src: url(data:application/font-woff2;charset=utf-8;base64,) format('woff2'),
        url('fonts/Optima-Regular.ttf') format('truetype');
    font-weight: normal;
    font-style: normal;
}

